//=require components/menu.js

$(document).ready(function () {

    // Burger menu
    $('.header__burger').click(function () {
        var burger = this;
        if ($(burger).hasClass('header__burger--active')) {
            $(burger).removeClass('header__burger--active');
            $('.header__bottomline').slideUp();
        } else {
            $(burger).addClass('header__burger--active');
            $('.header__bottomline').slideDown();
        }
    });

    // FAQ accardeon
    $('.faq__item:first-child').addClass('faq__item--active').find('.faq__answer').show();
    $('.faq__btn').click(function () {
        if ($(this).parent().hasClass('faq__item--active')) {
            $(this).text('+').parent().removeClass('faq__item--active').find('.faq__answer').slideUp();
        } else {
            $('.faq__items').find('.faq__item').removeClass('faq__item--active').find('.faq__answer').slideUp().next().text('+');
            $(this).text('â€“').parent().addClass('faq__item--active').find('.faq__answer').slideDown();
        };
    });


    // About-document slider
    $('#documents-carousel').owlCarousel({
        margin: 20,
        items: 5,
        dots: false,
        nav: true,
        navText: ['<div class="arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.about__arrow-block',
        loop: true,
        responsive: {
            992: {
                items: 5,

            },
            720: {
                items: 4,
                stagePadding: 50,
            },
            576: {
                items: 3,
                stagePadding: 50,
            },
            0: {
                items: 1,
                stagePadding: 50,
            }
        }
    });

    // Office slider
    $('#office-slider').owlCarousel({
        margin: 20,
        items: 3,
        nav: true,
        dots: false,
        navText: ['<div class="arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.about__arrow-block',
        loop: true,
        responsive: {
            992: {
                items: 3,

            },
            720: {
                items: 2,
            },
            576: {
                items: 3,
            },
            0: {
                items: 2,
            }
        }
    });

    // Sber slider
    $('#sber-slider').owlCarousel({
        nav: true,
        navText: ['<div class="arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.sber__arrow-block',
        loop: true,
        dots: false,
        responsive: {
            992: {
                items: 3,

            },
            720: {
                items: 2,
            },
            576: {
                items: 2,
            },
            0: {
                items: 1,
            }
        }
    });

    // Feedback slider
    $('#feedback-carousel').owlCarousel({
        margin: 20,
        items: 3,
        nav: true,
        navText: ['<div class="arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.feedback__arrow-block',
        loop: true,
        dots: false,
        responsive: {
            992: {
                items: 3,

            },
            720: {
                items: 3,
            },
            576: {
                items: 2,
            },
            0: {
                items: 1,
            }
        }
    });

    // About-top slider
    $('#about-top-carousel').owlCarousel({
        items: 1,
        nav: true,
        navText: ['<div class="about-top__arrow-left arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="about-top__arrow-right arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.about-top__arrow-block',
        loop: true,
        dots: false,
        autoplay: true,
        autoplayHoverPause: true

    });

    // Hero-slider
    $('#hero-slider').owlCarousel({
        items: 1,
        nav: false,
        loop: true,
        dots: true,
        autoplay: true,
        autoplayHoverPause: true

    });

    // Index-docs slider
    $('#index-doc-slider').owlCarousel({
        margin: 20,
        items: 5,
        nav: false,
        loop: true,
        dots: false,
        responsive: {
            992: {
                items: 5,

            },
            720: {
                items: 4,
                stagePadding: 50,
            },
            576: {
                items: 3,
                stagePadding: 50,
            },
            0: {
                items: 1,
                stagePadding: 50,
            }
        }
    });

    // About-document slider
    $('#index-part-slider').owlCarousel({
        nav: true,
        items: 1,
        dots: false,
        navText: ['<div class="arrow-left"><img src="img/vector-temp/arrow-left.png" alt=""></div>', '<div class="arrow-right"><img src="img/vector-temp/arrow-right.png" alt=""></div>'],
        navContainer: '.part__arrow-block',
        loop: true,
        responsive: {

            720: {
                items: 2
            },
        }

    });
});